      mirrorStyle.left  = parseInt(inputClientRect.left) + "px";
      mirrorStyle.top   = parseInt(inputClientRect.bottom - bodyClientRect.top) + "px";
      mirrorStyle.width = inputStyle.getPropertyValue("width");

